<template>
  <div>
    <el-upload
      class="upload-demo"
      ref="upload"
      action="http://localhost:9081/pipeLineAnalysis/uploadFile"
      accept=".json"
      multiple
      :file-list="fileList"
      :auto-upload="false"
      :on-success="handleSuccess"
      :on-progress="handleProgress"
    >
      <el-button slot="trigger">选取文件</el-button>
      <el-button style="margin-left: 10px;" type="primary" @click="submitUpload">上传到服务器</el-button>
      <el-button style="margin-left: 10px;" type="success" @click="updateServer">更新管线数据服务</el-button>
      <el-progress :text-inside="true" :stroke-width="18" :percentage="percentage"></el-progress>
    </el-upload>

    <el-table ref="filterTable" :data="tableData" style="width: 100%">
      <el-table-column prop="batch" width="180" label="管线批次"></el-table-column>
      <el-table-column prop="file" width="180" label="管线json文件"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-button @click="addNewPipeLine">新增管线批次</el-button>
    <el-dialog
      title="选择管线文件及批次"
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose"
    >
      <el-select v-model="value" placeholder="请选择json文件">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <el-input v-model="input" width="180" placeholder="请输入批次ID"></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="onAddNewPipeLine">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
/* eslint-disable */
const axios = require("axios");

export default {
  data() {
    return {
      tableData: [],
      fileList: [],
      options: [],
      value: "",
      input: 0,
      dialogVisible: false,
      percentage: 0
    };
  },
  mounted() {
    axios
      .get("http://localhost:9081/pipeLineAnalysis/configFile")
      .then(response => {
        // 更新table数据
        this.tableData = [];
        for (let i = 0; i < response.data.pipeLine.length; ++i) {
          let filename = response.data.pipeLine[i].file.split("/");
          this.tableData.push({
            batch: response.data.pipeLine[i].batch,
            file: filename[filename.length - 1]
          });
        }
      });
  },
  methods: {
    submitUpload() {
      this.$refs.upload.submit();
    },
    handleSuccess(response, file, fileList) {
      debugger;
      let allFileSuccess = true;
      for (let i = 0; i < fileList.length; ++i) {
        let upFile = fileList[i];
        if (100 !== upFile.percentage) {
          allFileSuccess = false;
          break;
        }
      }
      this.$notify({
        title: "成功",
        message: file.name + "文件上传成功",
        type: "success",
        duration: 2000
      });

      if (allFileSuccess) {
        this.$refs.upload.clearFiles();
        this.percentage = 0;
      }
    },
    updateServer() {
      // 更新服务器配置列表
      let data = [];
      for (let i = 0; i < this.tableData.length; ++i) {
        let batch = this.tableData[i].batch;
        let file = this.tableData[i].file;
        data.push({
          batch,
          file
        });
      }
      axios
        .get("http://localhost:9081/pipeLineAnalysis/updateConfigFile", {
          params: {
            data
          }
        })
        .then(response => {
          axios
            .get("http://localhost:9081/pipeLineAnalysis/startServer")
            .then(response => {
              // 服务启动成功
              this.$notify({
                title: "成功",
                message: "服务更新成功",
                type: "success",
                duration: 2000
              });
            });
        });
    },
    handleClose() {
      this.dialogVisible = false;
    },
    addNewPipeLine() {
      axios
        .get("http://localhost:9081/pipeLineAnalysis/fileList")
        .then(response => {
          // 更新选择文件列表
          this.options = [];
          for (let i = 0; i < response.data.length; ++i) {
            this.options.push({
              value: i.toString(),
              label: response.data[i]
            });
          }
          this.dialogVisible = true;
        });
    },
    onAddNewPipeLine() {
      debugger;
      // 确定批次
      let patchId = this.input;
      let filename = "";
      for (let i = 0; i < this.options.length; ++i) {
        if (this.options[i].value == this.value) {
          filename = this.options[i].label;
          break;
        }
      }

      // 构造新的tableData并发送给后台
      let update = false;
      for (let i = 0; i < this.tableData.length; ++i) {
        if (this.tableData[i].batch == patchId) {
          this.tableData[i].file = filename;
          update = true;
        }
      }
      if (!update) {
        this.tableData.push({
          batch: patchId,
          file: filename
        });
      }

      this.dialogVisible = false;
    },
    handleDelete(index, row) {
      this.tableData.splice(index, 1);
    },
    handleProgress(event, file, fileList) {
      this.percentage = ((event.loaded / event.total) * 100).toFixed(2);
      this.percentage = parseFloat(this.percentage);
    }
  }
};
</script>


